import { format, isValid, parseISO } from "date-fns"

export const humanFriendlyDateFormat = (dateStrIso, defaultFormatStr = 'E do MMM, yyyy hh:mm a') => {
    return format(parseISO(dateStrIso), defaultFormatStr)
}

export const formatMySql = (dateStr) => {
    return format(new Date(dateStr), "yyyy-MM-dd")
}

export const formatBytesToString = (bytes) => {
    if (bytes < 1024) {
        return `${bytes} bytes`
    }
    if (bytes > 1024 && bytes < 1024 * 1024) {
        return `${(bytes / 1024).toFixed(3)} KB`
    }
    if (bytes > 1024 * 1024 && bytes < 1024 * 1024 * 1024) {
        return `${(bytes / (1024 * 1024)).toFixed(3)} MB`
    }
    return `${(bytes / (1024 * 1024 * 1024)).toFixed(3)} GB`
}

export const formatUserName = (user) => {
    return [user?.firstName, user?.middleName, user?.lastName].filter(x => x).join(" ")
}

export const convertToDate = (strordate) => {
    if (typeof strordate === "object") {
        return strordate
    }else{
        try{
            return parseISO(strordate)
        }catch(e){
            return new Date(strordate)
        }
    }
}

export const ucFirst = (str) => {
    return str.charAt(0).toUpperCase() + str.slice(1);
}