import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import { isBefore, isDate, subDays, subYears } from "date-fns";
import React, { useEffect } from "react";
import { useState } from "react";
import { convertToDate, formatMySql } from "src/utils/commonHelper";
import { parseQueryParams, pushQueryParams } from "src/utils/routerHelper";
import { DateRangePicker } from 'react-date-range';
import { Button } from 'adminlte-2-react';
import Dialog from '../Dialog';

const DateFilter = ({
    label = "Filter by date",
    cols = 12,
    ...rest
}) => {
    const [showPicker, setShowPicker] = useState(false)
    const [state, setState] = useState([]);

    const getFilterValue = () => {
        const defaultStart = subDays(new Date(), 7)
        const defaultEnd = subDays(new Date(), 7)
        let start = parseQueryParams(rest.history, 'start', null)
        let end = parseQueryParams(rest.history, 'end', null)

        start = isDate(start) ? convertToDate(start) : defaultStart
        end = isDate(end) ? convertToDate(end) : defaultEnd

        if (!isBefore(start, end)){
            start = subDays(end, 7)
        }

        setState([
            {
                startDate: start,
                endDate: end,
                key: 'selection'
            }
        ])
    }

    useEffect(() => {
        getFilterValue();
        const unlisten = rest.history.listen(() => {
            getFilterValue();
        })
        return unlisten
    }, [])


    return (
        <div className={`col-md-${cols}`}>
            {state.length > 0 && <Button disabled text={`Showing results from ${formatMySql(state[0].startDate)} to ${formatMySql(state[0].endDate)}`} />}
            <Button type="success" onClick={() => setShowPicker(true)} icon="fa-calendar" text="Filter by Date" />
            <Dialog show={showPicker} size="lg" title="Choose dates"
                onDismiss={() => {
                    setShowPicker(false)
                }}
             onSuccess={() => {
                setShowPicker(false)
                pushQueryParams(rest.history, {
                    start: formatMySql(state[0].startDate),
                    end: formatMySql(state[0].endDate)
                })
            }} acceptText="Search">
                {state.length > 0 && <DateRangePicker
                    minDate={subYears(new Date(), 1)}
                    maxDate={new Date()}
                    onChange={item => {
                        setState([item.selection])
                    }}
                    showSelectionPreview={true}
                    moveRangeOnFirstSelection={false}
                    months={2}
                    ranges={state}
                    direction="horizontal"
                />}
            </Dialog>
        </div>
    )
}

export default DateFilter