import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { useEffect, useMemo, useState } from "react";
import UserService from "src/services/UserService";
import { USER_ROLES, USER_STATUSES } from "src/constants/global";
import CommonServerice from "src/services/CommonService";

const AdminListView = ({
  title = 'Super Users',
  description = 'here you can manage super users',
  currentUser,
  ...rest
}) => {

  const [locations, setLocations] = useState([])

  useEffect(() => {
    (async () => {
      setLocations(await CommonServerice.getLocationList())
    })();
  }, [])

  const fields = useMemo(() => {
    const _fields = [
      {
        id: "username",
        name: "username",
        label: "Username",
        required: true,
        placeholder: "Enter Username",
        type: "text",
        cols: 4,
      },
      {
        id: "password",
        name: "password",
        label: "Password",
        required: true,
        type: "password",
        cols: 4,
      },
      {
        id: "role",
        name: "role",
        label: "Role",
        type: "select",
        required: true,
        cols: 4,
        options: [USER_ROLES.ADMIN, USER_ROLES.SUPERADMIN].map(role => {
          return {
            key: role,
            value: role
          }
        })
      },
      {
        id: "locationId",
        name: "locationId",
        label: "Location",
        required: true,
        type: "select",
        options: locations.map(l => {
          return {
            key: l.id,
            value: l.title
          }
        }),
        cols: 3,
      },
      {
        id: "firstName",
        name: "firstName",
        label: "First Name",
        required: true,
        type: "text",
        cols: 3,
      },
      {
        id: "middleName",
        name: "middleName",
        label: "Middle Name",
        type: "text",
        cols: 3,
      },
      {
        id: "lastName",
        name: "lastName",
        label: "Last Name",
        type: "text",
        cols: 3,
      },
    ]
    return _fields
  }, [locations])

  return (
    <Content title={title} subTitle={description} browserTitle={"Manage CTMS Users"}>
      <Box type="primary" title="Displaying CTMS users" icon="fa-users">
        <Row>
          <Col md={12}>
            <DataGridComponent
              modalSize="lg"
              actionColumnWidth={280}
              canDeleteFn={(row) =>  row?.id !== currentUser?.id}
              {...rest}
              rowActionButtons={[
                {
                  text: 'Block',
                  icon: 'fa-times',
                  type: 'danger',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.ACTIVE && row.id !== currentUser?.id
                  },
                  onClick: async (id) => {
                    await UserService.blockUser(id)
                  }
                },
                {
                  text: 'Unblock',
                  icon: 'fa-check',
                  type: 'success',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.DISABLED && row.id !== currentUser?.id
                  },
                  onClick: async (id) => {
                    await UserService.unblockUser(id)
                  }
                }
              ]}
              entitySingle="Super User"
              entityPlural="Super Users"
              resource={`/api/v1/admin/superusers`}
              formOptions={{
                fields
              }}
              columns={[
                {
                  dataField: 'id',
                  text: 'User ID',
                  hidden: true,
                }, {
                  dataField: 'username',
                  text: 'Username',
                  sort: true,
                },
                {
                  dataField: 'role',
                  text: 'Role',
                  sort: true,
                },
                {
                  dataField: 'status',
                  text: 'Status',
                  sort: true,
                },
                {
                  dataField: 'firstName',
                  text: 'Name',
                  sort: true,
                  formatter: (c, row) => {
                    const youString = row.id === currentUser?.id ? ' (You) ': ''
                    return [row.firstName, row.middleName, row.lastName].filter(x => !!x).join(" ") + youString
                  }

                },
              ]}
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default AdminListView;
