import { Field, Form, Formik } from "formik";
import React, { forwardRef } from "react";
import ReactQuill from "react-quill";
import DatePicker from "react-datepicker";
import PhotoThumb from "./PhotoThumb";
import { format } from "date-fns";
import { Alert, Button, ButtonGroup, Col, Row } from "adminlte-2-react";
import { showGlobalModalNotification } from "src/utils/storeHelper";
import UploadService from "src/services/UploadService";
import "react-datepicker/dist/react-datepicker.css";
import { fieldsToLabelMap } from "src/constants/global";
import { ucFirst } from "src/utils/commonHelper";

const CustomDateInput = forwardRef(({ onClick, originalValue }, ref) => {
  return (
    <ButtonGroup>
      <Button className="ps-2 pe-2" disabled text={originalValue || "YYYY-MM-DD"} />
      <Button onClick={onClick} icon="fa-calendar" />
    </ButtonGroup>
  );
});
const FieldWrap = ({
  field = {},
  errors = {},
  touched = {},
  children = null,
  formErrors = {}
}) => {
  const shouldShowError =
    (errors[field.name] && touched[field.name]) ||
    (errors[field.name] && field.type === "richtext") || formErrors[field.name];
  return (
    <Col key={field.id} className={`mb-2 col-lg-${field.cols} col-sm-12 col-md-6`}>
      <div style={{marginBottom: 10}} className={`form-group ${shouldShowError ? 'has-error' : ''}`}>
        {!field?.hideLabel && <label htmlFor={field.id} className="form-label">
          {field.label} {field.required && <sup className="text-red">*</sup>}
        </label>
        }
        <div>
          {children}
          {/* {shouldShowError && (
            <p style={{fontSize: 12, fontWeight:'normal', marginBottom: 0}} className="text-red">{errors[field.name] || formErrors[field.name]}</p>
          )} */}
        </div>
      </div>
    </Col>
  );
};

const objectAsArray = (object) => {
  if (Array.isArray(object)) {
    return object;
  } else {
    const data = [];
    Object.keys(key => {
      data.push({ key, value: object[key] })
    })
    return data;
  }
}

const EntityForm = ({
  id = "",
  action = "",
  method = "POST",
  disabled = false,
  initialValues = {},
  validationSchema = null,
  fields = [],
  onSubmit = async (values, props) => { },
  editMode = false,
  entitySingle = 'record',
  submitTitle = null,
  formErrors = {},
  hideSubmit = false
}) => {

  if (!initialValues || fields.length < 1) {
    return <></>;
  }
  return (
    <Row>
      <Col md={12}>
        <Formik
          initialValues={initialValues}
          enableReinitialize
          onSubmit={(values, formConfig) => {
            const modifiedValues = {}
            Object.keys(initialValues).forEach(key => {
              if ((values[key] !== initialValues[key] && values[key] !== undefined) || `${key}` === "id") {
                modifiedValues[key] = values[key]
              }
            });
            onSubmit(modifiedValues, formConfig)
          }}
          validationSchema={validationSchema}
        >
          {({ errors, touched, values, setFieldValue }) => (
            <Form
              action={action}
              id={id || Date.now()}
              method={method}
              disabled={disabled}
            >
              {Object.keys(formErrors).length > 0 && (<Row>
                <Col className="col-lg-12 col-md-12">
                  <Alert type="danger">
                    <h4>
                      <strong>Please note:</strong>
                    </h4>
                    {Object.keys(formErrors).map(field => {
                      if (field === 'global'){
                        return <></>
                      }
                      const fieldLabel = fieldsToLabelMap[field] ? fieldsToLabelMap[field] : field.replace('Id', '')
                      return <div key={field}><strong>{ucFirst(fieldLabel)}</strong> - {formErrors[field]}</div>
                    })}
                  </Alert>
                </Col>
              </Row>)}
              <Row>
                {fields.map((field) => {
                  switch (field.type) {
                    case "fieldset":
                      return (
                        <h3 className="display-6 fs-6 pt-2 pb-2 fw-bold bg-secondary text-white">{field?.title}</h3>
                      );
                    case "select":
                      const options = objectAsArray(field?.options || [])
                      return (
                        <FieldWrap
                          key={`field-${field.id}-${entitySingle}`}
                          {...{ field, errors, touched }}
                          formErrors={formErrors}
                        >
                          <Field
                            as="select"
                            className={
                              errors[field.name] && touched[field.name]
                                ? "form-select rounded-0 is-invalid shadow-none form-control"
                                : "form-select form-control"
                            }
                            placeholder={field.placeholder || field.label}
                            name={field.name}
                            id={field.id}
                            onChange={(e) => {
                              field.onChange && field.onChange(e)
                              setFieldValue(field.name, e.nativeEvent.target.value)
                            }}
                          >
                            <option value={''}>{field.placeholder || field.label}</option>
                            {options.map((option) => (
                              <option
                                key={`${option.key}-${entitySingle}`}
                                value={option.key}
                              >
                                {option.value}
                              </option>
                            ))}
                          </Field>
                        </FieldWrap>
                      );
                    case "textarea":
                      return (
                        <FieldWrap
                          key={`field-${field.id}`}
                          {...{ field, errors, touched }}
                          formErrors={formErrors}
                        >
                          <Field
                            as="textarea"
                            className={
                              errors[field.name] && touched[field.name]
                                ? "form-control shadow-none is-invalid"
                                : "form-control shadow-none"
                            }
                            placeholder={field.placeholder || field.label}
                            name={field.name}
                            id={field.id}
                            onChange={(e) => {
                              field.onChange && field.onChange(e)
                              setFieldValue(field.name, e.nativeEvent.target.value)
                            }}
                          />
                        </FieldWrap>
                      );
                    case "checkbox_group":
                      const setCheckBoxGroupFieldValue = (e) => {
                        const existingValues = new Set(values[field.name].length > 0 ? values[field.name] : [])
                        if (e.target.checked) {
                          existingValues.add(parseInt(e.target.value))
                        } else {
                          existingValues.delete(parseInt(e.target.value))
                        }
                        setFieldValue(field.name, Array.from(existingValues))
                      }
                      return (
                        <FieldWrap
                          key={`field-${field.id}`}
                          {...{ field, errors, touched }}
                          formErrors={formErrors}

                        >
                          <div className="p-3 ps-3" style={{ height: `${field.height || 150}px`, backgroundColor: '', border: 'solid 1px #eee', overflow: 'auto' }}>
                            {field.options.map(option => {
                              return (
                                <div key={option.key} className="form-check">
                                  <input
                                    type="checkbox"
                                    className={
                                      errors[field.name] && touched[field.name]
                                        ? "form-check-input shadow-none is-invalid"
                                        : "form-check-input shadow-none"
                                    }
                                    name={`${field.name}`}
                                    checked={values[field.name].includes(option.key)}
                                    id={`${field.id}-${option.key}`}
                                    value={option.key}
                                    onChange={setCheckBoxGroupFieldValue}
                                  />
                                  <label className="form-check-label" htmlFor={`${field.id}-${option.key}`}>{option.value}</label>
                                </div>
                              )
                            })}
                          </div>
                        </FieldWrap>
                      );
                    case "richtext":
                      return (
                        <FieldWrap
                          key={`field-${field.id}`}
                          {...{ field, errors, touched }}
                          formErrors={formErrors}
                        >
                          <ReactQuill
                            theme="snow"
                            value={values[field.name]}
                            onChange={(value) => setFieldValue(field.name, value)}
                          />
                        </FieldWrap>
                      );
                    case "date":
                      return (
                        <FieldWrap
                          key={`field-${field.id}`}
                          {...{ field, errors, touched }}
                          formErrors={formErrors}
                        >
                          <DatePicker
                            showIcon
                            name={field.name}
                            className="d-flex"
                            onChange={(date) => {
                              field.onChange && field.onChange(date)
                              setFieldValue(
                                field.name,
                                format(date, "yyyy-MM-dd")
                              )
                            }
                            }
                            customInput={
                              <CustomDateInput
                                originalValue={values[field.name]}
                              />
                            }
                          />
                        </FieldWrap>
                      );
                    case "upload":
                      return (
                        <FieldWrap
                          key={`field-${field.id}`}
                          {...{ field, errors, touched }}
                          formErrors={formErrors}
                        >
                          <input onChange={async (e) => {
                            const file = e.nativeEvent?.target?.files[0]
                            if (!file) {
                              showGlobalModalNotification({
                                success: false,
                                message: "You've choosen any file",
                              })
                              return
                            }
                            const url = await UploadService.uploadFile(file)
                            if (!url) {
                              showGlobalModalNotification({
                                success: false,
                                message: "Unable to upload file, contact server admin."
                              })
                              return
                            }
                            setFieldValue(field.name, url)
                          }} id="fileinputRef" accept="image/*" type="file" name={"file"} style={{ opacity: 0, position: "absolute", left: -9999, top: -100 }} />
                          <div className="d-flex flex-wrap align-items-center justify-content-start">
                            <PhotoThumb
                              key={"no-selection"}
                              url={values[field.name] || values[field.default]}
                            />
                            <Button
                              onClick={() => {
                                document.getElementById('fileinputRef').click();
                              }}
                              icon="fa-edit"
                            />
                          </div>
                        </FieldWrap>
                      );
                    case "hidden":
                      return (
                        <Field
                          key={`field-${field.id}`}
                          autoComplete="off"
                          name={field.name}
                          type={"hidden"}
                          id={field.id}
                        />
                      );
                    default:
                      return (
                        <FieldWrap
                          key={`field-${field.id}`}
                          {...{ field, errors, touched }}
                          formErrors={formErrors}
                        >
                          <Field
                            autoComplete="off"
                            className={
                              errors[field.name] && touched[field.name]
                                ? "form-control rounded-0 shadow-none is-invalid"
                                : "form-control rounded-0 shadow-none"
                            }
                            placeholder={field.placeholder || field.label}
                            name={field.name}
                            type={field.type}
                            id={field.id}
                            onChange={(e) => {
                              field.onChange && field.onChange(e)
                              setFieldValue(field.name, e.nativeEvent.target.value)
                            }}
                          />
                        </FieldWrap>
                      );
                  }
                })}
              </Row>
              {!hideSubmit && (
                <>
                  <hr />
                  <div className="d-flex flex-row-reverse text-right">
                    <button
                      disabled={disabled}
                      onClick={() => window.scrollTo(0, 0)}
                      type="success"
                      className="btn rounded-0  btn-primary"
                    >
                      {submitTitle && <span>{submitTitle}</span>}
                      {!submitTitle && (
                        <span>
                          {editMode ? "Update" : "Create"} {entitySingle}
                        </span>
                      )}
                    </button>
                  </div>
                </>
              )}

            </Form>
          )}
        </Formik>
      </Col>
    </Row>
  );
};

export default EntityForm;
