import React, { useMemo } from "react";
import { clearQueryParams } from "src/utils/routerHelper";

const ClearFilterButton = ({
    history,
    title = 'Clear Filters',
    filtersToClear = ['locationId', 'start', 'end'],
    cols = 2
}) => {
    return (
        <div className={`col-md-${cols}`}>
            <span className="btn btn-danger" onClick={() => {
                clearQueryParams(history, filtersToClear)
            }}>
                {title}
            </span>
        </div>
    )
}

export default ClearFilterButton;